<template>
  <!--乡镇风采/江西省上饶市鄱阳县双港乡-->
  <!--lh-lg-->
  <div>
    <div class="container">
      <section class="content">
        <h1 class="lh-lg">江西省上饶市鄱阳县双港乡</h1>
        <h2 class="lh-lg">一、简介</h2>
        <p class="text-start lh-lg">
          双港镇位于鄱阳县西南部，为县域城郊乡镇，属滨湖地区，北濒我国第一大淡水湖——鄱阳湖，南临赣东北重要水道饶河，西与九江市都昌县毗邻。总区域面积285平方公里，下辖29个行政村，1个社区，102个自然村，168个村民小组，共有2万余户，总人口近10万，其中江西省最大的专业渔村——长山村（3500人口）属双港镇管辖。
        </p>
        <h2 class="lh-lg">二、著名景点</h2>
        <h3 class="lh-lg">鄱阳湖大草原景区</h3>
        <img src="@/assets/img/township/tsg/byhdcy.png" alt="鄱阳湖大草原景区"><br><br>
        <p class="text-start lh-lg">
          鄱阳湖大草原景区又名为无念岛，一是因为无念源于佛教用语，它阐述的是一种观念的转变，二是因为无念岛优越的自然风光能够给人带来回归自我，沉淀内心的完美感受。希望每一个来无念岛的朋友都能够“离岛无念”。<br>
          如果说无念岛是鄱阳湖大草原景区的精华，那么无念岛的精华应该是“无念八景”。它们分别是:原上绿翠、蓼子花海、芦荻迷宫、鸟鸣汀兰、夕阳醉晚、长山岚霭、星空营地和离岛无念。
        </p>
        <h3 class="lh-lg">鄱阳湖国家湿地公园</h3>
        <img src="@/assets/img/township/tsg/byhsdgy.png" alt="鄱阳湖国家湿地公园"><br><br>
        <p class="text-start lh-lg">
          鄱阳湖国家湿地公园以湿地为主体景观，集湖泊、河流、草洲、泥滩、岛屿等其他类型的复合型湿地公园。是世界六大湿地之一，亚洲湿地面积最大、湿地物种最丰富的国家级湿地公园，国家4A级景区，也是观赏鄱阳湖的最佳地带。
        </p>
        <p class="text-start lh-lg">
          鄱阳湖烟波浩淼，气势磅礴，公园内河流众多，溪水蜿蜒，农田蔓延，芦苇片片。湖光山色，景色幽静，环境优雅，空气清新，溶山水之灵气于一方，汇自然与人文为一体。湿地公园内集湿地文化、候鸟文化、农耕文化、饮食文化等人文景观资源于一体，形成了融多种风格文化景观为一体的湿地旅游景观。
        </p>
        <h3 class="lh-lg">双港塔</h3>
        <img src="@/assets/img/township/tsg/sgt.png" alt="双港塔"><br><br>
        <p class="text-start lh-lg">
          双港塔的传说 塔南系陡壁悬崖，浪涛拍岸，崖壁悬空。据传，塔为明万历年间（1573—1620）双港人京都御史陈了干所建，此人横行乡里，作恶多端，竟将安置塔顶的四力士无故杀害，刀落之时，塔顶亦不翼而飞，故双港塔至今无顶。
        </p>
        <p class="text-start lh-lg">
          双港塔层高为七,八面体,塔基12平米,自建造不久,便厄运累累,先是塔顶遭雷击坍塌,后因风雨侵蚀而受损,直至1998年大水,终因山体崩坍失去支撑而匐然倒塌。双港塔虽然消失了,但它留给鄱阳人的印象却是如此深刻。因为在数百里的鄱阳湖周边,至今还没有一处风光可以与它媲美。
        </p>
        <h2 class="lh-lg">三、特产</h2>
        <h3 class="lh-lg">鄱阳湖大闸蟹</h3>
        <img src="@/assets/img/township/tsg/dzx.png" alt="鄱阳湖大闸蟹"><br><br>
        <p class="text-start lh-lg">
          大闸蟹以青背、白肚、金爪、黄毛为显著特点，因大、肥、鲜、甜而闻名于世，且历史悠久，尤以九江地区鄱阳湖水域所产螃蟹极负盛名。唐代韩驹有《谢江州陆签判寄糖蟹》诗二首，到了宋代更有天台徐似道游庐山，吃鄱阳湖螃蟹，留下了传世佳句。
        </p><br><br>
        <p class="text-center lh-lg">
          文字撰写 | 周子路 <br>
          信息来源 | 百度百科、上饶市政府官网
        </p>
      </section>
